import { useState } from 'react';
import axios, { AxiosResponse, AxiosError, Method } from 'axios';

type UseApiProps<T extends { resultCode: string }> = {
  endpoint: string;
  method: Method;
};

type UseApiResult<T> = {
  data: T | null;
  loading: boolean;
  error: string | null;
  execute: (postData?: Record<string, any>) => Promise<void>;
};

const useApi = <T extends { resultCode: string }>({ endpoint, method }: UseApiProps<T>): UseApiResult<T> => {
  const [data, setData] = useState<T | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  // const baseUrl = process.env.REACT_APP_API_URL;
  const baseUrl = "http://3.37.137.19:8080";

  const execute = async (postData?: Record<string, any>): Promise<void> => {
    setLoading(true);
    try {
      const response: AxiosResponse<T & { resultMessage?: string }> = await axios({
        method,
        url: `${baseUrl}/${endpoint}`,
        data: postData,
      });

      if (response.data.resultCode === '0000') {
        setData(response.data);
      } else {
        setError(response.data.resultMessage || 'An error occurred');
      }
    } catch (error: any) {
      setError(error);
    } finally {
      setLoading(false);
    }
  };

  return { data, loading, error, execute };
};

export default useApi;
