/** @jsxImportSource @emotion/react */
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Card } from 'antd';
import { css } from "@emotion/react";
import { useWalletStore } from 'store/store';
import BaseButton from "components/BaseButton";
import image_proposal from "assets/images/image_proposal.png";

interface ChildProps {
  data?: string[];
}

const BaseCard: React.FC<ChildProps> = ({ data }) => {
  const navigate = useNavigate();
  const { ethersProvider } = useWalletStore();
  const [isHovered, setIsHovered] = useState(false);

  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };

  return (
    <div css={css`padding: 10px;`}
    >
      <Card
        hoverable
        style={{ width: 350, maxWidth: 400, height: 410, position: "relative" }}
        bodyStyle={{ padding: 0 }}
        cover={<img alt="example" src={image_proposal} css={css`max-height: 200px; object-fit: cover;`} />}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        onClick={() => navigate("/proposal/detail")}
      >
        <p css={css`
                font-size: 24px;
                font-weight: 700;
                background: linear-gradient(to right, #39945C, #82E5A8 );
                -webkit-background-clip: text;
                color: transparent;
                padding: 8px 32px 0px;
              `}
        >
          Honey Proposal
        </p>
        {isHovered ? (
          <>
            <div css={css`
                    display: grid;
                    grid-template-columns: repeat(2, 1fr);
                    justify-content: space-between;
                    padding: 0px 32px 50px;
                    white-space: nowrap;
                  `}
            >
              <p css={itemTitle}>Final Goal Amount</p>
              <p css={itemValue}>4718592.0</p>
              <p css={itemTitle}>Fundraiser</p>
              <p css={itemValue}>0xF2AC...a098</p>
              <p css={itemTitle}>Deposit Amount</p>
              <p css={itemValue}>9</p>
            </div>
            <BaseButton 
              text="GO INVEST"
              style={css`
                      width: 100%;
                      height: auto;
                      position: absolute;
                      bottom: 0;
                      border: 0;
                      color: #fff;
                      font-size: 20px;
                      background: linear-gradient(to right, #39945C, #82E5A8 );
                      border-radius: 0 0 8px 8px;
                      cursor: pointer;
                      padding: 14px 0px;
                    `}
            />
          </>
        ) : (
          <div css={css`
                  display: grid;
                  grid-template-columns: repeat(2, 1fr);
                  justify-content: space-between;
                  padding: 0px 32px 40px;
                  white-space: nowrap;
                `}
        >
          <p css={itemTitle}>Token Pool ID</p>
          <p css={itemValue}>01</p>
          <p css={itemTitle}>Staking Duration</p>
          <p css={itemValue}>24</p>
          <p css={itemTitle}>Annual Interest Rate</p>
          <p css={itemValue}>9%</p>
          <p css={itemTitle}>Token Pool Status</p>
          <p css={itemValue}>STARTED</p>
        </div>
        )}
      </Card>
    </div>
  );
};

export default BaseCard;

const itemTitle = css`
  font-size: 20px;
  color: #9e9e9e;
`;

const itemValue = css`
  font-size: 20px;
  color: #39945c;
  text-align: right;
`;
