/** @jsxImportSource @emotion/react */
import React, { useState, useEffect } from 'react';
import { css } from '@emotion/react';
import { Pagination } from 'antd';
import useApi from "hooks/useApi";
import headerImage from "assets/images/proposal/proposal_header.png";
import hyjelly from "assets/images/image_ character_hyjelly.png";
import BaseButton from 'components/BaseButton';
import BaseCard from "components/BaseCard";

const Proposal: React.FC = () => {
  const [selectedKeyword, setSelectedKeyword] = useState("#APR");
  const [currentPage, setCurrentPage] = useState(1);
  const endpoint = "v1/proposal?page=0&size=10&sort=NORMAL";
  const { data, loading, error, execute } = useApi({ endpoint, method: "GET" });

  useEffect(() => {
    execute();
    console.log(data);
  }, []);

  const handleChangePage = (page: number) => {
    setCurrentPage(page);
  };

  return (
    <div css={css`
              position: absolute;
              top: 0;
              width: 100%;
              height: 100%;
              background-color: #fff;
            `}
    >
      <div>
        <div css={css`
                  background-image: url(${headerImage});
                  background-repeat : no-repeat;
                  background-size : cover;
                  background-position: center center;
                  width: 100%;
                `}
        >
          <div css={css`
                    padding: 220px 80px 56px;
                  `}
          >
            <p css={css`
                    font-size: 32px;
                    background: linear-gradient(to right, #82E5A8, #39945C );
                    -webkit-background-clip: text;
                    color: transparent;
                  `}
            >
              Proposal
            </p>
            <p css={css`
                    font-size: 60px;
                    color: #fff;
                  `}
            >
              Token Pool Proposal
            </p>
          </div>
        </div>
        <div css={css`padding: 50px 80px 0px`}>
          <p css={css`
                  font-size: 32px;
                `}
          >A Service for Distributing Value</p>
          <p css={css`
                  font-size: 32px;
                  color: #39945C;
                `}
          >to Beneficiaries of HyperCycle.</p>
        </div>
        <img alt="metamong" src={hyjelly} css={css`max-width: 40%; position: absolute; top: 220px; right: 80px;`} />
      </div>
      <div css={css`
                padding: 80px;
              `}
      >
        <div>
          <h2 css={css`
                    font-size: 50px;
                    background: linear-gradient(to right, #6EE987, #39945C );
                    -webkit-background-clip: text;
                    color: transparent;
                  `}
          >
            Token Pool Proposal
          </h2>
        </div>
        <div css={css`padding: 40px 0px;`}>
          <BaseButton 
            text="#APR" 
            onClick={() => setSelectedKeyword("#APR")} 
            style={selectedKeyword === "#APR" ? selectedStyle : deSelectedStyle}
          />
          <BaseButton 
            text="#Crowd Funding End Date" 
            onClick={() => setSelectedKeyword("#Crowd Funding End Date")} 
            style={selectedKeyword === "#Crowd Funding End Date" ? selectedStyle : deSelectedStyle}
          />
          <BaseButton 
            text="#Duration" 
            onClick={() => setSelectedKeyword("#Duration")} 
            style={selectedKeyword === "#Duration" ? selectedStyle : deSelectedStyle}
          />
        </div>
        <div css={css`
                  display: grid;
                  grid-template-columns: repeat(3, 1fr);
                `}
        >
          <BaseCard />
          <BaseCard />
          <BaseCard />
        </div>
        <Pagination
          current={currentPage}
          pageSize={12}
          total={12}
          onChange={handleChangePage}
          style={{
            display: "inline-block",
            width: "100%",
            textAlign: "center",
            color: "#39945C",
            borderColor: "linear-gradient(to right, #39945C, #82E5A8)",
          }}
        />
      </div>
    </div>
  );
};

export default Proposal;

const selectedStyle = css`
  font-weight: 700;
  color: #fff;
  background-color: #39945C;
  border: 1px solid #39945C;
  margin-right: 20px;
`;

const deSelectedStyle = css`
  font-weight: 700;
  color: #3A945D;
  background-color: #fff;
  border: 1px solid  #3A945D;
  margin-right: 20px;
`;
